export function formatTime(isoString: string): string {
  try {
    const date = new Date(isoString);
    return new Intl.DateTimeFormat('zh-CN', {
      hour: '2-digit',
      minute: '2-digit',
      hour12: false
    }).format(date);
  } catch (e) {
    return isoString;
  }
}

// 3
export function formatRelativeTime(isoString: string): string {
  try {
    const date = new Date(isoString);
    const now = new Date();
    const diffInSeconds = Math.floor((now.getTime() - date.getTime()) / 1000);

    if (diffInSeconds < 60) return '';
    if (diffInSeconds < 3600) return `${Math.floor(diffInSeconds / 60)}`;
    if (diffInSeconds < 86400) return `${Math.floor(diffInSeconds / 3600)}`;
    if (diffInSeconds < 604800) return `${Math.floor(diffInSeconds / 86400)}`;
    
    return formatDateTime(isoString);
  } catch (e) {
    return isoString;
  }
} 